<template>
  <div class="row">

      <etiquetaNumero class="col"
      v-for="n in getNumerosAll" :key="n.numero"
      :numero = "n.numero"
      :ocupado = "n.ocupado"
      />
</div>
  
</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapActions, mapGetters } from 'vuex'

export default {
    components:{
        etiquetaNumero : defineAsyncComponent(()=> import('./etiquetaNumero.vue'))
    },
    computed:{
        ...mapGetters('numeros',['getNumerosAll'])
    },
    methods:{
        ...mapActions('numeros',['getNumeros'])
    },
    created(){
        this.getNumeros()
    }
}
</script>

